<div class="fixed top-0 left-0 w-screen h-[100vh] bg-zinc-700 opacity-90 overflow-x-hidden overflow-y-auto flex items-center justify-center" @click.self="close" @scroll="handleScroll"><div class="w-2/3 h-[5000px] bg-purple-100"></div></div><div class="fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-auto h-auto"><div class="text-4xl font-extrabold transition-all duration-300 ease-in-out" :class="[       data &gt; 200 &amp;&amp; data &lt; 500 ? 'scale-[2.5]' : 'scale-100',       data &gt; 700 &amp;&amp; data &lt; 1000 ? 'shake' : '',     ]">{{data}}</div></div>