<div class="w-full h-[100vh] flex flex-wrap items-center justify-center"><div class="drop-zone w-[300px] md:w-[500px] h-[300px] md:h-[500px] flex flex-col items-center justify-center cursor-pointer border-[#009578] border-4 rounded-2xl bg-[#e0ffb5]" v-if="!tableData.length" ref="fileDiv" :class="[       borderStyle ? 'border-solid' : 'border-dashed',     ]" @click="choseFile" @drop="dropFile" @dragover="dragOver" @dragleave="setBorder" @dragend="setBorder"><span>匯入檔案</span><span>xlsx檔(剛好三欄且無合併儲存格)</span><input class="drop-zone__input hidden" ref="fileInput" type="file" name="myFile" @change="changeFile"></div><div class="w-full md:w-3/4 h-auto px-1 flex flex-col items-center justify-center" v-else><el-table :data="nowData" style="width: 100%"><el-table-column v-for="(item, index) of tableTitle" :key="index" :prop="item.prop" :label="item.label" show-overflow-tooltip></el-table-column></el-table><el-pagination class="mt-2" small background layout="prev, pager, next" :total="tableData.length" :page-size="isMobile ? 8 : 10" @current-change="changePage" :current-page="nowPage"></el-pagination><button class="bg-green-500 text-white my-2 py-2 px-4 font-medium rounded-xl transition-all duration-300 hover:bg-green-400" @click="clear">清除</button></div></div>